import React, { Component } from "react";
import { getCetakUlang } from "../../actions/cetakUlangAction";
import { getUUID } from "../../actions/fakturAction";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { gagal } from "../../Alert";
import HeaderCard from "../../HeaderCard";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { renderField, renderFieldSelect } from "../../renderField";
import GlobalTabel from "../../GlobalComponent/GlobalTabel";
import { AxiosMasterGet } from "../../Axios";
import PrintNota from "../../Stocking/Component/PrintNota";
import { getcustomerList } from "../../actions/customerAction";
import { formatGram, formatRp } from "../../helper";
import { getToday } from "../../GlobalComponent/function";

class FormLaporanTolakanCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "jenis_trx",
          text: "Jenis Transaksi",
        },
        {
          dataField: "no_bon",
          text: "Nomor Bon",
        },
        {
          dataField: "total_bruto",
          text: "Total Bruto(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total_gross",
          text: "Total Gross(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total_netto",
          text: "Total Netto(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total_pkg",
          text: "Total Pkg(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total_rp",
          text: "Total Rp(Rp)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  onClick={() => this.getDetails(row)}
                >
                  <i className="fa fa-print mr-1"></i>
                  Print
                </button>
              </div>
            );
          },
        },
      ],
      data: [
        {
          tanggal: "2020-11-12",
        },
      ],
      listPenerimaan: [],
    };
  }

  getDetails(data) {
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = data.detail_barang;
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        ++i,
        ticket.kode_jenis,
        ticket.kadar_cetak,
        formatGram(ticket.p),
        formatGram(ticket.k),
        formatGram(ticket.pkg),
        formatGram(ticket.bruto),
        ticket.kadar_beli,
        formatGram(ticket.netto, 3),
        formatRp(ticket.ongkos_rp),
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    const footer = [
      "",
      "",
      "",
      "",
      "",
      formatGram(table.map((list) => list.pkg).reduce((a, b) => a + b, 0)),
      formatGram(table.map((list) => list.bruto).reduce((a, b) => a + b, 0)),
      "",
      formatGram(
        table.map((list) => list.netto).reduce((a, b) => a + b, 0),
        3
      ),
      formatRp(table.map((list) => list.ongkos_rp).reduce((a, b) => a + b, 0)),
    ];
    tableRows.push(footer);
    console.log(JSON.stringify(tableRows));
    var columTable = [
      "No",
      "Kode Model",
      "Cap Kode",
      "P",
      "K",
      "Bungkus(Gr)",
      "Berat(Gr)",
      "Harga(Gr)",
      "Murni(Gr)",
      "Ongkos(Rp)",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    PrintNota(
      "No Bon",
      data.no_bon.toUpperCase(),
      "Tanggal",
      data.tanggal,
      "Customer",
      this.state.printSupplier[0].kode_customer.toUpperCase(),
      "",
      "",
      "ADMIN",
      data.tanggal,
      "",
      columTable,
      "Tolakan Return Barang Supplier",
      tableRows
    );
  }
  componentDidMount() {
    this.props.dispatch(getcustomerList());
    this.props.dispatch(getUUID());
    this.props.dispatch(getCetakUlang());
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  pilihSupplier = (input) => (e) => {
    var supp = e.target.value ? e.target.value : "DEFALT | DEFAULT";
    var result_explode = supp.split("|");
    this.setState({
      kodeSupplier: result_explode[0],
      namaSupplier: result_explode[1],
    });
  };

  caridata() {
    AxiosMasterGet(
      `report/tolakan-customer/${this.props.kode_customer}&${this.props.tanggal_awal}&${this.props.tanggal_akhir}`
    )
      .then((res) =>
        this.setState({
          listPenerimaan: res.data && res.data[0].detail,
          printSupplier: res.data,
        })
      )
      .catch(() => gagal("Data Kosong"));
  }
  render() {
    return (
      <>
        <div className="container-fluid">
          <ol className="breadcrumb mb-2">
            <Link to="/cetakUlangBukti">
              <button type="button" className="btn btn-secondary">
                <i className="fa fa-angle-left mr-3" aria-hidden="true"></i>{" "}
                Back
              </button>
            </Link>
          </ol>
          <div className="card">
            <HeaderCard title="Cetak Ulang Bukti Tolakan Customer" />
            <div className="card-body">
              <br />
              <div className="row">
                <div className="col-lg-3">
                  <Field
                    name="tanggal_awal"
                    component={renderField}
                    type="date"
                    label="Mulai Dari"
                    placeholder="Masukan Mulai Dari"
                  />
                </div>
                <div className="col-lg-3">
                  <Field
                    name="tanggal_akhir"
                    component={renderField}
                    type="date"
                    label="Sampai Dengan"
                    placeholder="Masukan Sampai Dengan"
                  />
                </div>
                <div className="col-lg-4">
                  <Field
                    name="kode_customer"
                    component={renderFieldSelect}
                    options={this.props.customer.map((data) => {
                      let hasil = {
                        value: data.kode_customer,
                        name: data.nama_customer,
                      };
                      return hasil;
                    })}
                    type="text"
                    label="Kode Customer"
                    placeholder="Masukan Kode Customer"
                  />
                </div>
                <div className="col-md-12 col-lg-12 mt-3">
                  <div className="row">
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.caridata()}
                        >
                          <i className="fa fa-search mr-1"></i>
                          Cari Data
                        </button>
                      </div>
                    </div>
                    <div className="col-lg-12">
                      <GlobalTabel
                        bootstrap4
                        keyField={this.state.listPenerimaan}
                        data={this.state.listPenerimaan}
                        columns={this.state.columns}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

FormLaporanTolakanCustomer = reduxForm({
  form: "FormLaporanTolakanCustomer",
})(FormLaporanTolakanCustomer);
const selector = formValueSelector("FormLaporanTolakanCustomer");
export default connect((state) => {
  const { tanggal_awal, tanggal_akhir, kode_customer } = selector(
    state,
    "tanggal_awal",
    "tanggal_akhir",
    "kode_customer"
  );
  return {
    customer: state.provinsi.customer,
    tanggal_akhir: tanggal_akhir,
    tanggal_awal: tanggal_awal,
    kode_customer: kode_customer,
    initialValues: {
      tanggal_awal: getToday(),
      tanggal_akhir: getToday(),
    },
  };
})(FormLaporanTolakanCustomer);
